<style></style>
<template>
    <div>
        <p>
            <router-link to="/topics">
                    &lt; Back to topics
            </router-link>
        </p>
        <b-card
            id="show-topic"
        >
            <template slot="header">
                <h3>{{ title }}
                 <router-link
                    :id="'edit-topic-'+topic.id+'-btn'" 
                    class="btn btn-secondary float-right btn-sm" 
                    :to="'/topics/'+topic.id+'/edit'"
                >
                    Edit
                </router-link>
                </h3>
           </template>
            <div v-if="this.topics">
                <p><strong>Gene Symbol</strong>: {{ topic.gene_symbol }}</p>
                <p><strong>Expert Panel</strong>: {{ (topic.expert_panel) ? topic.expert_panel.name : '--'}}</p>
                <p><strong>Curator</strong>: {{ (topic.curator) ? topic.curator.name : '--'}}</p>
                <p><strong>MonDO ID</strong>: {{ (topic.mondo_id) ? topic.mondo_id : '--'}}</p>
                <p><strong>Status</strong>: {{ (topic.topic_status) ? topic.topic_status.name : '--'}}</p>
                <p><strong>Notes</strong>: {{ (topic.notes) ? topic.notes : '--' }}</p>
                <p><strong>Notes on disease entity</strong>: {{ (topic.disease_entity_notes) ? topic.disease_entity_notes : '--' }}</p>
            </div>
            <phenotype-list :topic="topic" :gene-symbol="topic.gene_symbol"></phenotype-list>
        </b-card>
    </div>
</template>
<script>
    import { mapGetters, mapActions } from 'vuex'
    import PhenotypeList from './Phenotypes/List'

    export default {
        props: ['id'],
        components: {
            'phenotype-list': PhenotypeList
        },
        computed: {
            ...mapGetters('topics', {
                topics: 'Items',
                getTopic: 'getItemById'
            }),            
            title: function () {
                let title = 'Topic: ';
                if (this.topic.gene_symbol) {
                    title += this.topic.gene_symbol
                    if (this.topic.mondo_id) {
                        title += ' / ' + this.topic.mondo_id
                    }
                    if (this.topic.expert_panel) {
                        title += ' for '+this.topic.expert_panel.name
                    }
                }
                return title;
            },
            topic: function(){
                if (this.topics.length == 0) {
                    console.log("no topics");
                    return {}
                }

                return this.getTopic(this.id);
            }

        },
        methods: {
            ...mapActions('topics', {
                fetchTopic: 'fetchItem'
            })
        },
        mounted: function () {
            this.fetchTopic(this.id);
        }
    }
</script>


